import { useState } from 'react';

import { useForm } from 'react-hook-form';

import { Link } from 'react-router-dom';

import styled from 'styled-components';

import useUserStore from '../hooks/useUserStore';

import Button from './common/Button';
import Input from './common/Input';

const Container = styled.article`
  display: flex;
  justify-content: center;
  align-items: center;

  height: 100%;
`;

const Title = styled.h2`
  padding-block: 4px;

  border-bottom: 1px solid ${((props) => props.theme.colors.primary)};

  font-size: ${((props) => props.theme.size.h1)};
  font-weight: 700;
  text-align: center;
`;

const Inputs = styled.div`
  min-width: 390px;

  margin-block: 60px;

  color: ${((props) => props.theme.text.gray)};
`;

const InputWrapper = styled.div`
  & + & {
    margin-top: 24px;
  }
`;

const Label = styled.label`
  display: block;

  margin-bottom: 8px;

  font-size: 15px;
  font-weight: 700;
`;

const ErrorMessage = styled.p`
  margin-top: 8px;

  font-size: 15px;

  color: ${((props) => props.theme.text.red)};
`;

const DefaultMessage = styled.p`
  margin-top: 8px;

  font-size: 15px;
`;

const SignupCompleted = styled(Container)`
  h2 {
    margin-bottom: 16px;

    font-size: ${((props) => props.theme.size.h1)};
    font-weight: 700;
    text-align: center;
  }

  p {
    margin-bottom: 40px;

    font-size: ${((props) => props.theme.size.h5)};
    text-align: center;
  }
`;

export default function SignupForm() {
  const userStore = useUserStore();

  const { register, handleSubmit, formState: { errors } } = useForm();

  const [isPasswordNotMatch, setIsPasswordNotMatch] = useState(false);

  const onSubmit = async (data) => {
    const {
      name, username, password, confirmPassword,
    } = data;

    if (password !== confirmPassword) {
      setIsPasswordNotMatch(true);
      return;
    }

    await userStore.signup({
      name, username, password, confirmPassword,
    });
  };

  if (userStore.isSignupSuccess) {
    return (
      <SignupCompleted>
        <div>
          <h2> </h2>
          <p>
               .
            <br />
                 .
          </p>
          <Link to="/login">
            <Button type="button">
              
            </Button>
          </Link>
        </div>
      </SignupCompleted>
    );
  }

  return (
    <Container>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Title>SIGN UP</Title>
        <Inputs>
          <InputWrapper>
            <Label htmlFor="input-name">:</Label>
            <Input
              id="input-name"
              type="text"
              name="name"
              error={(errors.name && errors.username && errors.password
                && errors.confirmPassword) || errors.name}
              {...register('name', {
                required: true,
                pattern: /^[-|-|-]{3,7}$/,
              })}
            />
            {errors.name
              ? (<ErrorMessage>  </ErrorMessage>)
              : (<DefaultMessage>3~7   </DefaultMessage>)}
          </InputWrapper>
          <InputWrapper>
            <Label htmlFor="input-username">:</Label>
            <Input
              id="input-username"
              type="text"
              name="username"
              error={(errors.name && errors.username && errors.password
                && errors.confirmPassword) || errors.username || userStore.isSignupFailed}
              {...register('username', {
                required: true,
                pattern: /^[a-z0-9]{4,16}$/,
              })}
            />
            {errors.username
              ? (<ErrorMessage>  </ErrorMessage>)
              : userStore.isSignupFailed
                ? (<ErrorMessage>    </ErrorMessage>)
                : (<DefaultMessage>/, 4~16  </DefaultMessage>)}
          </InputWrapper>
          <InputWrapper>
            <Label htmlFor="input-password">:</Label>
            <Input
              id="input-password"
              type="password"
              name="password"
              error={(errors.name && errors.username && errors.password
                && errors.confirmPassword) || errors.password}
              {...register('password', {
                required: true,
                pattern: /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$ %^&*-]).{8,}$/,
              })}
            />
            {errors.password
              ? (<ErrorMessage>  </ErrorMessage>)
              : (<DefaultMessage>8  (), ,    </DefaultMessage>)}
          </InputWrapper>
          <InputWrapper>
            <Label htmlFor="input-confirm-password"> :</Label>
            <Input
              id="input-confirm-password"
              type="password"
              name="confirmPassword"
              error={(errors.name && errors.username && errors.password
                && errors.confirmPassword) || errors.confirmPassword}
              {...register('confirmPassword', { required: true })}
            />
          </InputWrapper>
          {errors.confirmPassword
          && (<ErrorMessage>  </ErrorMessage>)}
          {isPasswordNotMatch
          && <ErrorMessage>  </ErrorMessage>}
        </Inputs>
        <Button type="submit"></Button>
      </form>
    </Container>
  );
}
